.calculator {
  width: 300px;
  margin: auto;
  padding: 1rem;
  border-radius: 12px;
  box-shadow: 0 0 15px rgba(0,0,0,0.2);
  background-color: #f8f8f8;
  text-align: center;
}

.display {
  width: 100%;
  height: 50px;
  font-size: 1.5rem;
  text-align: right;
  margin-bottom: 1rem;
  padding: 0.5rem;
  border: 1px solid #ddd;
  border-radius: 5px;
}

.buttons {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 10px;
}

.btn {
  height: 50px;
  font-size: 1.2rem;
  cursor: pointer;
  background-color: #e0e0e0;
  border: none;
  border-radius: 5px;
}

.btn:hover {
  background-color: #d0d0d0;
}
